@tailwind base;
@tailwind components;
@tailwind utilities;

.container {
    position: relative;
    text-align: center;
    width: 100%;
}

.svg-container {
    position: absolute;
    left: 50%;
    top: 25.1%;
    transform: translate(-50%, -50%);
    width: 165px;
}

.back-svg {
    width: 600px;
}

.container svg {
    transform: scale(0.42);
}

@media (max-width: 1300px) {
    .back-svg {
        width: 400px;
    }

    .container svg {
        transform: scale(0.27);
    }

    .svg-container {
        width: 108px;
        top: 26.4%;
    }
}

@media (max-width: 1023px) {
    .back-svg {
        width: 400px;
    }

    .container svg {
        transform: scale(0.27);
    }

    .container {
        min-width: 100%;
    }
}

@media (max-width: 950px) {
    .back-svg {
        width: 400px;
    }

    .container svg {
        transform: scale(0.27);
    }

    .svg-container {
        top: 25%;
    }
}

@media (max-width: 700px) {
    .back-svg {
        width: 350px;
    }

    .container svg {
        transform: scale(0.235);
    }
    .svg-container {
        width: 95px;
    }
}

@media (max-width: 600px) {
    .back-svg {
        width: 300px;
    }

    .container svg {
        transform: scale(0.2);
    }

    .svg-container {
        top: 25.5%;
        width: 80px;
    }
}

@media (max-width: 280px) {
    .back-svg {
        width: 250px;
    }

    .container svg {
        transform: scale(0.17);
    }

    .svg-container {
        top: 26.5%;
        width: 68px;
    }
}
